import { UserService } from 'src/app/services/user.service';
import { RemoteStatsType } from './../../models/remote-stats';
import { AlertService } from "./../../services/alert.service";
import { ProviderService } from "src/app/services/provider.service";
import { SelectIconComponent } from "./../../common/select-icon/select-icon.component";
import { Icons } from "./../../services/env.service";
import { EnvService } from "src/app/services/env.service";
import { ProviderGroup } from "./../../models/provider-group";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { ModalController, LoadingController } from "@ionic/angular";
import { Component, OnInit, Input } from "@angular/core";
import { from, of } from "rxjs";
import { concatMap, catchError } from "rxjs/operators";

@Component({
  selector: "app-adm-add-provider-category",
  templateUrl: "./adm-add-provider-category.component.html",
  styleUrls: ["./adm-add-provider-category.component.scss"],
})
export class AdmAddProviderCategoryComponent implements OnInit {
  @Input() isEdit: boolean = false;
  @Input() category: ProviderGroup = new ProviderGroup();
  statsType = RemoteStatsType;
  selectIconModal: ModalController;
  title = "";
  constructor(
    private modal: ModalController,
    private iconModal: ModalController,
    private providerService: ProviderService,
    private loading: LoadingController,
    private alert: AlertService,
    private env: EnvService,
    private userService:UserService
  ) {}
  dismiss() {
    this.modal.dismiss();
  }
  selectIcon() {
    this.presentLoading().subscribe((result) => {
      this.presentSelectIconModal().subscribe((presented) => {
        this.loading.dismiss();
      });
    });
  }
  formValid(): boolean {
    return this.category.name != "" && this.category.icon_name != "";
  }
  presentSelectIconModal() {
    return from(
      this.iconModal.create({
        component: SelectIconComponent,
        componentProps: {},
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {
          let iconName = data["dataa"];
          if (iconName != "") {
            this.category.icon_name = data["data"];
          }
        });
        return from(modal.present());
      })
    );
  }
  presentLoading() {
    return from(
      this.loading.create({
        mode: "ios",
        translucent: true,
      })
    ).pipe(
      concatMap((modal) => {
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
  ngOnInit() {
    if (!this.isEdit) {
      
      this.title = "گروه جدید";
    } else {
      this.userService.updateRemoteStats().subscribe();
      this.title = "ویرایش گروه";
    }
  }
  submit() {
    if (this.isEdit) {
      this.env.somethingIsPending("editing provider category");
      this.providerService
        .editCategory(this.category)
        .pipe(
          catchError((error) => {
            this.alert.presentError("ویرایش گروه مشاوره با مشکل مواجه شد.");

            return of(false);
          })
        )
        .subscribe((result) => {
          this.env.somethingIsDonePending("editing provider category");
          if (result) {
            this.alert.presentSuccess("گروه با موفقیت ویرایش شد.");
          }
        });
    } else {
      this.env.somethingIsPending("adding provider category");
      this.providerService
        .addCategory(this.category)
        .pipe(
          catchError((error) => {
            this.alert.presentError("ثبت گروه جدید با مشکل مواجه شد.");

            return of(false);
          })
        )
        .subscribe((result) => {
          this.env.somethingIsDonePending("adding provider category");
          if (result) {
            this.alert.presentSuccess("گروه با موفقیت ثبت شد.");
          }
        });
    }
    this.dismiss();
  }
}
